<template>
  <div id="Software">
    <el-row v-for="(rowData, index) in productData" :key="index">
      <el-col :span="7" v-for="(item, index) in rowData" :key="index" :offset="index > 0 ? 1 : 0">
        <el-card :body-style="{ padding: '0px' }" style="height:260px;margin-bottom:20px;">
          <img :src="item.img" class="image" />
          <div style="padding: 5px;">
            <div style="margin: 7px;">{{item.name}}</div>
            <div class="bottom clearfix">
              <!-- <el-button type="text" class="button" @click="jumpDetail(item)">查看详情</el-button> -->
              <router-link slot="reference" :to="'/productcenter/huke/at-65'">查看详情</router-link>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { metrohmData } from "../json/metrohm";

export default {
  name: "Product_Metrohm",
  data() {
    return {
      productData: metrohmData
    };
  },

  methods: {
    jumpDetail(item) {
      console.log(item.name);
    }
  }
};
</script>
<style scoped>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 10px;
  line-height: 12px;
}

.button {
  padding: 0 10px;
  float: right;
}

.image {
  width: 100%;
  display: block;
  height: 190px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
</style>




